import React, { Component } from 'react'
import { Row, Col, Layout, Modal, Table, Tag, Typography } from 'antd'
import HeaderBar from 'components/HeaderBar'
import ChatBox from 'components/chatbox'

import {
  // HomeOutlined,
  // ConsoleSqlOutlined,
  // FileTextOutlined,
  // UnorderedListOutlined,
  // MessageOutlined,
  ArrowUpOutlined,
  ArrowDownOutlined,
  PlusSquareFilled,
  FunnelPlotOutlined
} from '@ant-design/icons'
import {
  AddPlot
} from 'components/AddPlot'
import {
  AddAgent
} from 'components/AddAgent'
import { Bar } from 'react-chartjs-2'

// import { UserOutlined, LaptopOutlined, NotificationOutlined } from '@ant-design/icons';
// const { SubMenu } = Menu;
const { Header, Content, Footer, Sider } = Layout

class UserHome extends Component {
  constructor () {
    super()
    this.addNewPlot = this.addNewPlot.bind(this)
    this.addNewAgent = this.addNewAgent.bind(this)
    this.state = {
      tableData: [
        {
          name: 'Deepan',
          designation: 'Manager',
          email: 'deepanmania@gmail.com',
          mobile: 9999999999,
          percent: 18,
          status: 'increase',
          city: 'Madurai',
          role: 'Admin',
          thumbnail: ''
        },
        {
          name: 'Deepan',
          designation: 'Manager',
          email: 'deepanmania@gmail.com',
          mobile: 9999999999,
          percent: 24,
          status: 'decrease',
          city: 'Madurai',
          role: 'Sales Manager',
          thumbnail: ''
        }
      ],
      addNewPlot: false,
      visibleAddAgent: false
    }
  }

  addNewPlot () {
    this.setState({ addNewPlot: !this.state.addNewPlot })
  }

  addNewAgent () {
    this.setState({ visibleAddAgent: !this.state.visibleAddAgent })
  }

  render () {
    const state = {
      labels: ['January', 'February', 'March',
        'April', 'May', 'June', 'July'],
      datasets: [
        {
          backgroundColor: '#7e31ed',
          // borderColor: 'rgba(0,0,0,1)',
          label: 'performance',
          barThickness: 20,
          borderWidth: 2,
          data: [65, 59, 80, 81, 56, 100, 90]
        }
      ]
    }

    const columns = [{
      title: "Agent's List",
      dataIndex: 'name',
      key: 'name',
      render: (name, record) => (
        <div>
          <img className="table-thumbnail" src='data:image/png;base64,'/>
          <span className="table-name">
            <span className="table-inner-name">{record.name}</span><br />
            <span className="table-designation">{record.designation}</span>
          </span>
        </div>
      )
    },
    {
      dataIndex: 'email',
      key: 'email',
      render: (email) => (
        <span className="table-email">{email}</span>
      )
    },
    {
      dataIndex: 'mobile',
      key: 'mobile'
    },
    {
      dataIndex: 'percent',
      key: 'percent',
      render: (percent, record) => (
        // eslint-disable-next-line no-unused-expressions
        <span className={(record.status === 'increase' ? 'status-increase' : 'status-decrease')}>{percent}%{(record.status === 'increase' ? <ArrowUpOutlined /> : <ArrowDownOutlined />)}</span>
      )
    },
    {
      title: <PlusSquareFilled className="table-icon" style={{
        fontSize: '20px'
      }} onClick={this.addNewAgent} />,
      dataIndex: 'city',
      key: 'city'
    },
    {
      title: <FunnelPlotOutlined style={{
        fontSize: '20px'
      }}/>,
      dataIndex: 'role',
      key: 'role'
    }
    ]
    // const addNewPlot = () => {
    //   setNewPlot(!newPlot)
    // }
    return (
      <Layout>
        <HeaderBar>
          <Layout>
            {/* <Sider width={80} className="site-layout-background">
              <Menu
                mode="inline"
                defaultSelectedKeys={['1']}
                defaultOpenKeys={['sub1']}
                style={{ height: '100%', borderRight: 0 }}
              >
                <Menu.Item key="1" className="sidemenu">
                  <ConsoleSqlOutlined className="menuitem"/>
                </Menu.Item>
                <Menu.Item key="2" className="sidemenu">
                  <FileTextOutlined className="menuitem"/>
                </Menu.Item>
                <Menu.Item key="3" className="sidemenu">
                  <UnorderedListOutlined className="menuitem"/>
                </Menu.Item>

              </Menu>
            </Sider> */}
            <Layout>
              {/* <Breadcrumb style={{ margin: '16px 0' }}>
              <Breadcrumb.Item>Home</Breadcrumb.Item>
              <Breadcrumb.Item>List</Breadcrumb.Item>
              <Breadcrumb.Item>App</Breadcrumb.Item>
            </Breadcrumb> */}
              <Content
                className="site-layout-background"
                style={{
                  margin: 0,
                  minHeight: '1080px'
                }}
              >
                <div className="rectangle">
                  <div style={{
                    padding: '2rem'
                  }}>
                    <div className="body-header">
                        Welcome back Admin
                    </div>
                    <div className="body-content" style={{ fontSize: '14px' }}>
                    You’ve earned 80%  in this week !
                    Keep it up and improve your goals Lorem Ipsum is simply
                    dummy text of the printing and typesetting industry.
                    </div>
                  </div>
                </div>
                <Modal
                  visible={this.state.addNewPlot}
                  title="Add new plot"
                  onCancel={this.addNewPlot}
                  footer={null}
                  width="40%"
                  centered
                >
                  <AddPlot/>
                </Modal>
                <Modal
                  visible={this.state.visibleAddAgent}
                  title="Add new agent"
                  onCancel={this.addNewAgent}
                  footer={null}
                  width="40%"
                  centered
                >
                  <AddAgent/>
                </Modal>
                <Row justify="left">
                  <Col span="8">
                    <div className="box-content" style={{
                      backgroundColor: '#7571c7'
                    }}>
                      <Row justify="left">
                        <Col span="6">
                          <div className="letter-box">
                            <span className="letter">N</span>
                          </div>
                        </Col>
                        <Col span="6">
                          <span className="count">
                          198
                          </span>
                          <span className="count-desc"> New plots added</span>
                        </Col>
                      </Row>
                      <div className="oval"></div>
                      <div className="small-oval"></div>
                      <button className="box-button" onClick={this.addNewPlot}> + Add Plot</button>
                    </div>
                  </Col>
                  <Col span="8">
                    <div className="box-content" style={{
                      backgroundColor: '#5dca88'
                    }}>
                      <Row>
                        <Col span="6">
                          <div className="letter-box">
                            <span className="letter">A</span>
                          </div>
                        </Col>
                        <Col span="6">
                          <span className="count">
                          256
                          </span>
                          <span className="count-desc">Available Plots</span>
                        </Col>
                      </Row>
                      <div className="oval"></div>
                      <div className="small-oval"></div>
                    </div>
                  </Col>
                  <Col span="8">
                    <div className="box-content" style={{
                      backgroundColor: '#ff707c'
                    }}>
                      <Row>
                        <Col span="6">
                          <div className="letter-box">
                            <span className="letter">B</span>
                          </div>
                        </Col>
                        <Col span="6">
                          <span className="count">
                        198
                          </span>
                          <span className="count-desc">Booked Plots</span>
                        </Col>
                      </Row>
                      <div className="oval"></div>
                      <div className="small-oval"></div>
                    </div>
                  </Col>
                </Row>
                <div className="row-gap"></div>
                <Row>
                  <Col span="12">
                    <div className="admin-page-column-left">
                      <Table dataSource={this.state.tableData} columns={columns} />
                    </div>
                  </Col>
                  <Col span="12">
                    <div className="admin-page-column-right">
                      <Bar
                        data={state}
                        options={{
                          title: {
                            display: true,
                            text: 'Overall Performance',
                            fontSize: 20
                          },
                          legend: {
                            display: true,
                            position: 'right'
                          }
                        }}
                      />
                    </div>
                  </Col>
                </Row>
              </Content>
            </Layout>
          </Layout>
          <ChatBox/>
        </HeaderBar>
      </Layout>
    )
  }
}

export default UserHome
